.About, .Contact, .Project {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15%;
  padding-right: 25%;
  text-align: left;
}

span, a {
  font-style: italic;
  color: blue;
  text-decoration: none;
}

.para h1 {
  margin-bottom: 2;
}

.Project h1 {
  font-size: 5vh;
  margin: 0 0 2vh 0;
}



